.navbar {
  box-shadow: 0px 3px 5px grey;
  background-color: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(5px);
}

.navbar .ml-auto {
  margin-right: auto!important;
  margin-left: 0!important;
}

.navbar a {
  color: #343a40;
  text-decoration: none!important;
  margin: 2px 9px 2px 9px;
}

footer {
  padding:40px 0;
  background-color:#ffffff;
  color:#4b4c4d;
}
  
footer ul {
  padding:0;
  list-style:none;
  text-align:center;
  font-size:18px;
  line-height:1.6;
  margin-bottom:0;
}

footer li {
  padding:0 10px;
}

footer ul a {
  color:inherit;
  text-decoration:none;
  opacity:0.8;
}

footer ul a:hover {
  opacity:1;
}

footer .social {
  text-align:center;
  padding-bottom:25px;
}

footer .social > a {
  font-size:24px;
  width:40px;
  height:40px;
  line-height:40px;
  display:inline-block;
  text-align:center;
  border-radius:50%;
  border:1px solid #ccc;
  margin:0 8px;
  color:inherit;
  opacity:0.75;
}

footer .social > a:hover {
  opacity:0.9;
}

footer .copyright {
  margin-top:15px;
  text-align:center;
  font-size:13px;
  color:#aaa;
  margin-bottom:0;
}